body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
}
html {
  height: 100%;
}

#signout {
  color: #2196f3;
  cursor: pointer;
}

#previewPic {
  width: "300px";
}
.vetItem {
  width: 250px;
}

.friendlyItem {
  /* width: 150px; */
  height: 200px;
}

.galleryItem {
  height: 400px;
}

.photo {
  width: 100%;
}

.photo-item {
  -webkit-box-shadow: 2px 2px 4px 0 #ccc;
          box-shadow: 2px 2px 4px 0 #ccc;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  margin: 0 0 1.5em;
  padding: 1em;
  position: relative;
  width: 100%;
}

.photo-list {
  -webkit-column-gap: 1.5em;
          column-gap: 1.5em;
  font-size: 0.85em;
  margin: 1.5em 0;
  padding: 0;
}

@media only screen and (min-width: 400px) {
  .photo-list {
    -webkit-column-count: 2;
    column-count: 2;
  }
}

@media only screen and (min-width: 767px) {
  .photo-list {
    -webkit-column-count: 3;
    column-count: 3;
  }
}

@media only screen and (min-width: 991px) {
  .photo-list {
    -webkit-column-count: 4;
    column-count: 4;
  }
}

@media only screen and (min-width: 1199px) {
  .photo-list {
    -webkit-column-count: 5;
    column-count: 5;
  }
}

.photo-modal {
  /* left: 50%; */
  /* position: absolute;  */
  text-align: center;
  /* top: 50%; */
  /* transform: translateX(-50%) translateY(-50%); */
}

.table-exif {
  font-size: 12px;
}

.homeGalleryData {
  height: 300px;
}

.titlePreview {
  color: white;
  font-weight: bold;
  position: absolute;
  top: 40%;
  height: 60%;

  opacity: 0;
}
.previewCard:hover {
  opacity: 0.75;
}

.titlePreview:hover {
  opacity: 1;
}

/* Navbar */

body {
  padding-top: 65px;
}

.navbar-light {
  background-color: white;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
}

@media (min-width: 992px) {
  .navbar-collapse {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
  }
}

/*  */


.container {
  min-height: 78vh;
}
@media (min-width: 1445px){
  .container{
    min-height: 92vh;
  }
}
.parentGoogleMap {
  position: relative;

  height: 600px;
  margin-bottom: 0;
  width: 80%;
}

.cardScroll {
  overflow: scroll;
}



